<h2 id="基础数据类型和引用数据类型" tabindex="-1"><a class="header-anchor" href="#基础数据类型和引用数据类型" aria-hidden="true">#</a> 基础数据类型和引用数据类型</h2>
<blockquote>
<ul>
<li>基础数据类型表示简单的数字或字符</li>
</ul>
</blockquote>
<p>byte：在内存中占8位(bit)，即1个字节，取值范围-128~127，默认值0<br>
short：短整型，在内存中占16位，即2个字节，取值范围-32768~32767，默认值0<br>
int：在内在中占32位，即4个字节，取值范围-2147483648~2147483647，默认值0<br>
long：在内存中占64位，即8个字节-2^63~2^63-1，默认值0L<br>
float：在内存中占32位，即4个字节，用于存储带小数点的数字（与double的区别在于float类型有效小数点只有6~7位），默认值0<br>
double：在内存中占64位，即8个字节，默认值0<br>
char：在内存中占占16位，即2个字节，取值范围0~65535，默认值为空<br>
boolean：占1个字节，用于判断真或假（仅有两个值，即true、false），默认值false</p>
