<h2 id="相关知识" tabindex="-1"><a class="header-anchor" href="#相关知识" aria-hidden="true">#</a> 相关知识</h2>
<h2 id="静态区" tabindex="-1"><a class="header-anchor" href="#静态区" aria-hidden="true">#</a> 静态区</h2>
<p>保存自动全局变量和 static 变量（包括 static 全局和局部变量）。静态区的内容在总个程序的生命周期内都存在，由编译器在编译的时候分配。</p>
<h2 id="堆区" tabindex="-1"><a class="header-anchor" href="#堆区" aria-hidden="true">#</a> 堆区</h2>
<p>一般由程序员分配释放，由 malloc 系列函数或 new 操作符分配的内存，其生命周期由 free 或 delete 决定。在没有释放之前一直存在，直到程序结束，由OS释放。其特点是使用灵活，空间比较大，但容易出错</p>
<h2 id="栈区" tabindex="-1"><a class="header-anchor" href="#栈区" aria-hidden="true">#</a> 栈区</h2>
<p>由编译器自动分配释放，保存局部变量，栈上的内容只在函数的范围内存在，当函数运行结束，这些内容也会自动被销毁，其特点是效率高，但空间大小有限</p>
<h2 id="文字常量区" tabindex="-1"><a class="header-anchor" href="#文字常量区" aria-hidden="true">#</a> 文字常量区</h2>
<p>常量字符串就是放在这里的。 程序结束后由系统释放。</p>
